import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HrmanagementComponent } from './hrmanagement.component';


const routes: Routes = [
    {
        path: '',
        component: HrmanagementComponent,
        children: [
            {
                path: '',
                redirectTo: 'notes',
                pathMatch: 'full'
            },

            {
                path: 'notes',
                loadChildren: () => import('./notes/notes.module').then(mo => mo.NotesModule)
            },
            {
                path: 'peringatan',
                loadChildren: () => import('./peringatan/peringatan.module').then(mo => mo.PeringatanModule)
            },
            {
                path: 'resign',
                loadChildren: () => import('./resign/resign.module').then(mo => mo.ResignModule)
            },
            {
                path: 'promosi',
                loadChildren: () => import('./promosi/promosi.module').then(mo => mo.PromosiModule)
            },
            {
                path: 'dipecat',
                loadChildren: () => import('./dipecat/dipecat.module').then(mo => mo.DipecatModule)
            },
        ]
    }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class HrmanagementRoutingModule { }
