import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { HrmanagementRoutingModule } from './hrmanagement-routing.module';
import { PerfectScrollbarModule } from 'ngx-perfect-scrollbar';
import { WidgetsComponentsModule } from 'src/app/components/widgets/widgets-components.module';
import { NzLayoutModule } from 'ng-zorro-antd';
import { LayoutModule } from 'src/app/components/layout/layout.module';
import { HrmanagementComponent } from './hrmanagement.component';
import { RouterModule } from '@angular/router';


@NgModule({
    declarations: [HrmanagementComponent],
    imports: [
        CommonModule,
        HrmanagementRoutingModule,
        RouterModule,
        PerfectScrollbarModule,
        WidgetsComponentsModule,
        NzLayoutModule,
        LayoutModule,
    ],
})

export class HrmanagementModule { }
